import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  vehicletypesAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Vehicletypes/vehicletypesSlice";
import NumericInput from "../../../components/data-entry/inputs/numberInput";
export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddVehicletypes = (props) => {
  const [form] = Form.useForm();
  function ChangeHandeler(e) {
    console.log("change Handeler ", e);
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  const wheeltypeList = [
    { id: 1, name: "2 Wheeler" },
    { id: 2, name: "3 Wheeler" },
    { id: 3, name: "4 Wheeler" },
  ];

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.selectedData?.name,
      wheels: props.selectedData?.wheels,
    };
    if (obj.name != null || obj.name == "") {
      props.vehicletypesAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const countryToUpdate = {
      id: props.selectedData.id,
      name: props.selectedData?.name,
      wheels: props.selectedData?.wheels,
    };
    props.vehicletypesAdd(countryToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      Vehicle Type
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.selectedData.name,
      wheels: props.selectedData.wheels
    })
  }, [props.selectedData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.selectedData.id == null) {
            handleAdd();
          }
          else {
            handleUpdate();
          }
        }}>
        <Form.Item label="Vehicle Type:"
          name="name"
          rules={[
            { required: true, message: "Please input your Vehicle Type!" },
          ]}
          hasFeedback>
          <Input
            id="Vehicle Types"
            name="name"
            style={{ width: "100%" }}
            placeholder="Vehicle Type"
            value={props.selectedData?.name}
            onChange={ChangeHandeler}
          />
        </Form.Item>

        <Form.Item label="Wheels :"
          name="wheels"
          rules={[
            { required: true, message: "Please select your Wheels!" },
          ]}
          hasFeedback>
          <Select
            showSearch
            filterOption={(input, option) =>
              option.children.toLowerCase().includes(input.toLowerCase())
            }
            placeholder="Select  Wheels"
            optionFilterProp="Wheels"
            onChange={(value) => {
              props.InputChangeValue({
                key: "wheels",
                value: value,
              });
            }}
            value={
              props.selectedData?.wheels == null
                ? ""
                : props.selectedData?.wheels
            }
          >
            {wheeltypeList.map((wheels, index) => {
              return (
                <Option key={index} value={wheels.id}>
                  {wheels.name}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ vehicletypes }) {
  return {
    selectedData: vehicletypes.rowdata,
    model: vehicletypes.model,
  };
}

export default connect(mapStateToProps, {
  vehicletypesAdd,
  isModelVisible,
  InputChangeValue,
})(AddVehicletypes);
